product{
  text-align: center;
  font-family: bebas;
}
product h1{
  margin-left: 12.5%;
  font-size: 72px;
  text-align: left;
}
.product-grid
{
  font-family: helvethaica;
  display: flex;
  width: 75%;
  margin: auto;
  column-gap: 20px;
  margin-bottom: 100px;
}
.product-type-selected
{
  font-size: 42px;
  background-color: #583C2E;
  width: 266px;
  padding: 5px 10px;
  color: white;
  border-radius: 26px;
  margin-top: 20px;
  line-height: 42px;
  cursor: pointer;
  border: #583C2E solid 5px;
}
.product-type
{
  font-size: 42px;
  color: #583C2E;
  width: 266px;
  padding: 5px 10px;
  background-color: white;
  border-radius: 26px;
  margin-top: 20px;
  line-height: 42px;
  cursor: pointer;
  border: #583C2E solid 5px;
}
.product-type:hover
{
  font-size: 42px;
  background-color: #583C2E;
  width: 266px;
  padding: 5px 10px;
  color: white;
  border-radius: 26px;
  margin-top: 20px;
  line-height: 42px;
  transition: 0.2s;
}
.product-size-box
{
  box-shadow: 0 0 10px gray;
  border-radius: 26px;
  width: 90%;
  height: 720px;
  margin-top: 20px;
  margin-bottom: 0px;
  padding: 53px 0px;
  margin-left:30px;
}
.product-size-box h1
{
  text-align: center;
  margin-left: 0;
}
.product-size-box h2
{
  font-family: saraban;
  text-align: center;
  font-size: 36px;
}
.product-size-grid
{
  display: grid;
  grid-template-columns: 50fr 1fr 50fr;
  width: 85%;
  column-gap: 50px;
  justify-content: space-between;
  margin: auto;
}
.product-size img{
  position: absolute;
  border-radius: 30px;
  width: 180px;
  border: #583C2E solid 5px;
  border-bottom: #583C2E solid 30px;
  z-index: 5;
  margin-bottom: 0px;
  transform: translateX(-90px);
}
.product-size img:hover{
  width: 220px;
  transform: translateX(-110px);
  position: absolute;
  margin-left: -0px;
  z-index: 11;
  transition: 0.5s;
}
.product-size h2{
  font-size: 28px;
  color: white;
  
}
.product-item-grid{
  width: 80%;
  margin-left: 10%;
  margin-top: 10px;
  margin-bottom: 20px;
  display: grid;
  grid-template-columns: 1fr  1fr;
  column-gap: 90px;
  row-gap: 40px;
}
.vertical-line
{
  margin-top: 14px;
  height: 470px;
  width: 5px;
  background-color: gray;
}
.packaging
{
  width: 100%;
  background-color: #F5DAA3;
  padding: 80px 0px;
}
.packaging h1{
  font-family: bebas;
  font-size: 72px;
  margin-left: 12.5%
}
.packaging img{
  border-radius: 26px;
  width: 340px;
  border: #583C2E solid 5px;
  border-bottom: #583C2E solid 40px;
  z-index: 1;

}
.flex
{
  width: 100%;
  display: flex;
  justify-content: space-around;
  text-align: center;
}
.packaging-flex{
  display: flex;
  justify-content: space-between;
  width: 75%;
  margin: auto;
  margin-top: 40px;
  margin-bottom: 50px;
}
.company-slide
{
  margin: auto;
  width: 40%;
  margin-bottom: 80px;
}
.company
{
  margin-top: 80px;
}
.company h1{
  font-family: bebas;
  margin-left: 12.5%;
  font-size: 72px;
  text-align: left;
  margin-bottom: 40px;
}
.company-detail
{
  font-family: saraban;
  width: 70%;
  margin: auto;
  margin-bottom: 100px;
}
.company-detail h1{
  font-family: saraban;
  font-size: 36px;
  font-weight: bold;
}
.detail-flex
{
  display: flex;
  flex-direction: row;
}
.detail-grid
{
  display : grid;
  grid-template-columns: 2fr 10fr;
  column-gap: 20px;
  margin-left: 12.5%;
  
}
.detail-grid h2
{
  font-size: 24px;
}
.map-tab
{
  margin-top: -50px;
  border: black solid 1px;
  box-shadow: 0 0 5px ;
}
.map-tab button
{
  width: 50%;
  font-size: 24px;
  border: none;
  padding: 4px 50px;
  background-color: darkgray;
}
#selected
{
  background-color: white;
}
.map-img{
  margin: auto;
  text-align: center;
}
.map-img img
{
  margin : auto;
  width: 500px;
  padding: 20px;
}
.map-box
{
  border: black solid 2px;
}
.faqs
{
  background-color: #F5DAA3;
  padding: 80px 0px;
}
.faqs-grid
{
  padding: 45px 69px;
  border-radius: 40px;
  box-shadow: inset 0 0 10px gray;
  background-color: white;
  width: 70%;
  margin: auto;
  display: grid;
  grid-template-columns: 1fr 30fr;
  column-gap: 50px;
  row-gap: 0px;
}
.faqs h2
{
  font-family: saraban;
  font-size: 32px;
  font-weight: bold;
}.faqs h3
{
  font-family: saraban;
  font-size: 32px;
  margin-bottom: 60px;
}
.faqs h1,h2,h3
{
  line-height: 40px;
}
.faqs br
{
  display: block;
  margin-bottom: 20px;
}
footer
{
  background-color: #583C2E;
  padding: 80px 0px;
  overflow: hidden;
  height: 400px;
}
.contact-us-grid
{
  display: grid;
  width :70%;
  margin: auto;
  grid-template-columns: 1fr 1fr 1fr;
  column-gap: 50px;
}
.contact-us-grid h1
{
  color: #F5CC76;
